import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';

import { ServiceAgreement, UserInfo } from '../user-context-menu.service';

export interface UserContextMenuInfoCustomizableContext {
  userInfo: UserInfo | undefined;
  currentContext: ServiceAgreement | undefined;
}

@Component({
  selector: 'bb-user-context-menu-info',
  templateUrl: './user-context-menu-info.component.html',
})
export class UserContextMenuInfoComponent {
  hostRef = this;
  @Input() userInfo: UserInfo | undefined;
  @Input() currentContext: ServiceAgreement | undefined;
}

@Directive({
  selector: '[bbUserContextMenuInfoCustomizable]',
})
export class UserContextMenuInfoCustomizableDirective extends BbTemplate<
  UserContextMenuInfoComponent,
  UserContextMenuInfoCustomizableContext
> {}
